import React from 'react';
import axios from 'axios';
import { useState } from 'react';
import Stack from '@mui/material/Stack';
import Switch from './Components/Switch';
import MuiAlert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import FormInput from './Components/FormInput';
import CopyToClipboardButton from './Components/CopyIcon';
import { ReactMultiEmail, isEmail } from 'react-multi-email';

import './App.css';
import 'react-multi-email/dist/style.css';
import './css/MultiEmail.css';
import './css/TextArea.css';
import './css/ToggleButton.css';
import './css/shortenURL.css';
import './css/CopyIcon.css';

const App = () => {
  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

  const [values, setValues] = useState({
    email: '',
    subject: '',
  });

  const [ccEmails, setCCEmails] = useState([]);
  const [bccEmails, setBCCEmails] = useState([]);
  const [focused, setFocused] = useState(false);

  const [body, setBody] = useState('');
  const [isToggled, setIsToggled] = useState(false);

  const [shortenedLink, setShortenedLink] = useState('');
  const [tempLink, setTempLink] = useState('');

  const [successGenerated, setSuccessGenerated] = useState(false);

  const inputs = [
    {
      id: 'receiver',
      name: 'receiver',
      type: 'email',
      placeholder: 'Recipient Email',
      errorMessage: 'Invalid Email address format!',
      label: 'To',
      required: true,
    },
    {
      id: 'subject',
      name: 'subject',
      type: 'text',
      placeholder: 'Title',
      errorMessage: 'Title is required for an Email!',
      label: 'Subject',
      required: true,
    },
  ];

  const handleSubmit = e => {
    e.preventDefault();
  };

  const onChange = e => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };

  const handleGenerateClick = e => {
    e.preventDefault();

    const replaceNewLine = body.replaceAll(/\n/g, '%0D%0A');
    const listCCEmails = ccEmails.join(',');
    const listBCCEmails = bccEmails.join(',');

    var results = '';

    const encodeSubject = encodeURIComponent(values.subject);
    const configSubject = encodeSubject.replaceAll('%20', '%2B');
    const encoodeBody = encodeURIComponent(replaceNewLine);
    const configBody = encoodeBody.replaceAll('%20', '%2B');

    // PC Requested
    if (isToggled === false) {
      results =
        'https://mail.google.com/mail/u/0/?tf=cm%26to=' +
        values.receiver +
        '%26su=' +
        configSubject +
        '%26body=' +
        configBody;

      if (listCCEmails.length > 1) {
        results += '%26cc=' + listCCEmails;
      }

      if (listBCCEmails.length > 1) {
        results += '%26bcc=' + listBCCEmails;
      }

      // results += '%26ds=1%26tf=cm';
    }

    // Mobile Requested
    else {
      const configMobileSubject = configSubject.replaceAll('%2B', '%2520');
      const configMobileBody = configBody.replaceAll('%2B', '%2520');

      results =
        'mailto:' +
        values.receiver +
        '?subject=' +
        configMobileSubject +
        '%26body=' +
        configMobileBody;

      if (listCCEmails.length > 1) {
        results += '%26cc=' + listCCEmails;
      }

      if (listBCCEmails.length > 1) {
        results += '%26bcc=' + listBCCEmails;
      }
    }

    setTempLink(results);
    fetchData(results);
    setSuccessGenerated(true);
    //console.log(results);
  };

  // Validate the inputs should not be blank
  const validateForm = () => {
    return (
      inputs.every(input => values[input.name]) &&
      body.length > 0 &&
      isEmail(values.receiver)
    );
  };

  // Shorten the URL
  const fetchData = async userInput => {
    try {
      const response = await axios(
        `https://api.shrtco.de/v2/shorten?url=${userInput}`
      );
      setShortenedLink(response.data.result.full_short_link);
      //alert('Your generated link is: ' + response.data.result.full_short_link);
      //setShortenedLink(response.data.result.full_short_link);
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <div className="app">
      <form onSubmit={handleSubmit}>
        <h1>Composed-Ready Email Link Generator</h1>

        {inputs.map(input => (
          <FormInput
            key={input.id}
            {...input}
            value={values[input.name]}
            onChange={onChange}
          />
        ))}

        <div className="EmailInput">
          <label>[CC] Carbon Copy</label>
          <ReactMultiEmail
            id="cc"
            placeholder="CC Email(s)"
            emails={ccEmails}
            onChange={(_emails: string[]) => {
              setCCEmails(_emails);
            }}
            autoFocus={false}
            onFocus={() => setFocused(true)}
            onBlur={() => setFocused(false)}
            getLabel={(email, index, removeEmail) => {
              return (
                <div data-tag key={index}>
                  <div data-tag-item>{email}</div>
                  <span data-tag-handle onClick={() => removeEmail(index)}>
                    x
                  </span>
                </div>
              );
            }}
          />
        </div>

        <div className="EmailInput">
          <label>[BCC] Blind Carbon Copy</label>
          <ReactMultiEmail
            id="bcc"
            placeholder="BCC Email(s)"
            emails={bccEmails}
            onChange={(_emails: string[]) => {
              setBCCEmails(_emails);
            }}
            autoFocus={false}
            onFocus={() => setFocused(true)}
            onBlur={() => setFocused(false)}
            getLabel={(email, index, removeEmail) => {
              return (
                <div data-tag key={index}>
                  <div data-tag-item>{email}</div>
                  <span data-tag-handle onClick={() => removeEmail(index)}>
                    x
                  </span>
                </div>
              );
            }}
          />
        </div>

        <div className="textArea">
          <label>Body</label>
          <textarea
            id="body"
            name="body"
            placeholder="Content"
            maxLength={1800}
            value={body}
            onChange={e => setBody(e.target.value)}
            required
          />
        </div>

        <p id="characterCounts">{body.length}/1800</p>

        <div className="combineContainer">
          <div className="formInput" id="shortenURL">
            <label>Shorten URL</label>
            <div id="urlSection">
              <input
                className="shortenURL"
                type="text"
                placeholder=""
                value={shortenedLink}
                disabled={true}
              />
              <CopyToClipboardButton
                shortenedLink={shortenedLink}
              ></CopyToClipboardButton>
            </div>
          </div>

          <div className="toggleButtonsContainer">
            <label>PC | Mobile</label>
            <Switch
              isToggled={isToggled}
              onToggle={() => setIsToggled(!isToggled)}
              rounded={true}
            />
          </div>
        </div>

        <button
          id="generate"
          disabled={!validateForm()}
          onClick={handleGenerateClick}
        >
          Generate
        </button>
      </form>
      <p id="copyright">
        &copy; Wong Yan Zhi, Published on 27/3/2023 || v1.1 All Rights Reserved
      </p>

      <Stack spacing={2} sx={{ width: '100%' }}>
        <Snackbar
          open={successGenerated}
          autoHideDuration={3000}
          onClose={() => setSuccessGenerated(false)}
        >
          <Alert
            onClose={() => setSuccessGenerated(false)}
            severity="success"
            sx={{ width: '100%' }}
          >
            Sucessfully Generated Short Link!
          </Alert>
        </Snackbar>
      </Stack>
    </div>
  );
};

export default App;
